import axios from "axios";
import {ProfileType} from "../types/type";


const instance = axios.create({
    withCredentials: true,
    baseURL: 'https://social-network.samuraijs.com/api/1.0/',

    headers: {
        "API-KEY": "523d3abb-bc6d-4ef5-bfc5-dc3da0b1b40a",
       " Access-Control-Allow-Origin":  "*"
    }

})

export const usersAPI = {
    getUsers(currentPage = 1, pageSize = 15) {
        return instance.get(`users?page=${currentPage}&count=${pageSize}`)
            .then(response => {
                return response.data;
            })
    },

    unfollow(userId: number) {
        return instance.delete(`follow/${userId}`)
    },

    follow(userId: number) {
        return instance.post(`follow/${userId}`)
    },
    getProfile(userId: number) {
        console.warn('Obsolete method. Please');
        return profileAPI.getProfile(userId);
    }
}

export const profileAPI = {
    getProfile(userId: number) {
        return instance.get(`profile/` + userId)
    },

    getStatus(userId: number) {
        return instance.get(`profile/status/` + userId)
    },

    updateStatus(status: string) {
        return instance.put(`profile/status`, { status: status })
    },
    savePhoto(photoFile: any) {
        const formData = new FormData()
        formData.append('image', photoFile)
        return instance.put(`profile/photo`, formData, {
            headers: {
                "Content-type": 'multipart/form-data'
            }
        })
    },
    saveProfile(profile: ProfileType) {
        return instance.put(`profile`, profile)
    }
}
export enum ResultCodesEnum {
    Success = 0,
    Error = 1
}
export enum ResultCodesForCapchaEnum {
    CapchaIsRequred = 10
}
type MeResponseType = {
    data: {
        id: number
        login: string
        email: string
    }
    messages: Array<string>
    resultCode: ResultCodesEnum
}
type LoginResponseType = {
    data: {
        userId: number
    }
    messages: Array<string>
    resultCode: ResultCodesEnum | ResultCodesForCapchaEnum
}
export const authAPI = {
    me() {
        return instance.get<MeResponseType>(`auth/me`).then(res => res.data)
    },
    loginUser(email: string, password:string, rememberMe: boolean, captcha: null | string) {
        return instance.post<LoginResponseType>(`auth/login`, {email, password, rememberMe, captcha})
            .then(res => res.data)
    },
    logout() {
        return instance.delete(`auth/login`)
    },

}
authAPI.me().then((res) => res.data)

export const securityAPI = {
    getCaptchaUrl() {
        return instance.get(`security/get-captcha-url`)
    }

}




