import React from 'react';
import './App.css';
import Navbar from "./components/Navbar/Navbar";
import {BrowserRouter, Redirect, Route, Switch, withRouter} from "react-router-dom";
import Login from "./components/Login/Login";
import {connect, Provider} from "react-redux";
import {compose} from "redux";
import {initializeApp} from "./Redux/app-reducer";
import Preloader from "./components/common/Preloader/Preloader";
import UsersContainer from "./components/Users/UsersContainer";
import HeaderContainer from "./components/Header/HeaderContainer";
import store, {AppStateType} from "./Redux/redux-store";
import {withSuspense} from "./hoc/withSuspense";
import {createTheme, ThemeProvider} from '@mui/material';
import {Footer} from './components/Footer/Footer';

const DialogsContainer = React.lazy((): any => import('./components/Dialogs/DialogsContainer'));
const ProfileContainer = React.lazy((): any => import('./components/Profile/ProfileContainer'));

const SuspendedDialogs = withSuspense(DialogsContainer)
const SuspendedProfile = withSuspense(ProfileContainer)

type MapsPropsType = ReturnType<typeof mapStateToProps>
type DispatchPropsType = {
    initializeApp: () => void
}

class App extends React.Component<MapsPropsType & DispatchPropsType> {
    componentDidMount() {
        this.props.initializeApp()
    }

    componentWillUnmount() {
    }

    render() {
        if (!this.props.initialized) {
            return <Preloader/>
        }

        return (
            <div className='external-wrapper'>
                <div className='app-wrapper'>
                    <HeaderContainer/>
                    <Navbar/>
                    <div className='wrapper_content'>
                        <Switch>
                            <Route exact path='/'
                                   render={() => <Redirect to={'/profile'}/>}/>
                            <Route path='/profile/:userId?'
                                   render={() => <SuspendedProfile/>}/>
                            <Route path='/Dialogs' render={() => <SuspendedDialogs/>}/>
                            <Route path='/Users' render={() => <UsersContainer/>}/>
                            <Route exact path='/Login' render={() => <Login/>}/>
                        </Switch>
                    </div>
                    <Footer/>
                </div>
            </div>
        )
            ;
    }
}

const mapStateToProps = (state: AppStateType) => ({
    initialized: state.app.initialized
})


let AppContainer = compose<React.ComponentType>(
    withRouter,
    connect(mapStateToProps, {initializeApp}))(App)

const theme = createTheme({
    palette: {
        primary: {
            main: '#fff'
        },
        secondary: {
            main: '#ef5350'
        }
    },
    typography: {
        fontFamily: "-apple-system",
    },
    spacing: [0, 4, 8, 16, 32, 64, 128, 256],
})

const ReactApp: React.FC = () => {
    return <BrowserRouter>
        <Provider store={store}>
            <ThemeProvider theme={theme}>
                <AppContainer/>
            </ThemeProvider>
        </Provider>
    </BrowserRouter>
}

export default ReactApp